body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
  background-color: black;
  max-height: 100vh;
  overflow: hidden;
}

.App {
  display: flex;
  justify-content: center;
  align-items: center;
  max-height: 100vh;
  width: 100%;
  overflow: hidden;
  background-color: black;
}

.calc-wrapper {
  width: 100%;
  min-height: 100vh;
}

.row {
  display: flex;
  width: 100%;
}
.button-wrapper {
    height: 15vh;
    display: flex;
    justify-content: center;
    align-items: center;
    font-weight: lighter;
    font-size: 2em;
    background-color: #e0e1e6;
    color: #888888;
    flex: 1 1;
    outline: 1px solid #888888;
}
  
.operator {
    background-color: #fe9241;
    color: #ffffff;
    font-weight: 500;
}

.button-wrapper:hover {
    cursor: pointer;
}

.input {
    min-height: 22vh;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    font-weight: bold;
    font-size: 1.4em;
    background-color: #000000;
    color: #ffffff;
    padding: 0.5em;
    outline: 1px solid #888888;
  }
.clear-btn {
  min-height: 15vh;
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: lighter;
  font-size: 1.6em;
  background-color: #c5c3cd;
  color: #000000;
  flex: 1 1;
  outline: 1px solid #888888;
}

.clear-btn:hover {
  cursor: pointer;
}
